.relatedproducts{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;
}

.relatedproducts h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
}

.relatedproducts hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}

.relatedproducts-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
}

@media screen and (max-width: 1200px) { 
    .relatedproducts {
        height: 1250px;
    }
    .relatedproducts h1 {
    font-size: 40px;
    font-weight: 200;
    }
    .relatedproducts hr {
        display: none;
    }
    .relatedproducts-item {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 900px) { 
    .relatedproducts {
        height: 2300px;
    }
    .relatedproducts-item { 
        grid-template-columns: repeat(1, 1fr);
    }

}
